
#app {
  font-family: 'Quattrocento Sans', sans-serif;
  font-size: 18px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #455a64;
}
body {
  margin: 0px;
  background-color: #cfd8dc;
}

.container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: #cfd8dc;
}
.sub {
  text-align: center;
  margin-top: -30px;
  font-size: 16px;
}
.content {
  max-width: 800px;
}
.cardlist {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: reverse;
      -ms-flex-direction: column-reverse;
          flex-direction: column-reverse;
}
.single-card {
  margin: 10px;
  margin-bottom: 40px;
  background-color: #eceff1;
  padding: 30px;
  -webkit-box-shadow: 5px 8px #546e7a33;
          box-shadow: 5px 8px #546e7a33;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.to-col {
  min-width: 100px;
  -webkit-box-flex: 3;
      -ms-flex-positive: 3;
          flex-grow: 3;
  padding-right: 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.date {
  padding-top: 15px;
}
.message-col {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  padding-left: 10px;
}
.shuffle-button {
  position: fixed;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  left: 48px;
  bottom: 48px;
  width: 48px;
  height: 48px;
  border-radius: 24px;
  background-color: #455a64dd;
  color: #eceff1;
  text-align: center;
  -webkit-box-shadow: 4px 4px 5px #455a6499;
          box-shadow: 4px 4px 5px #455a6499;
  opacity: 0.3;
}
.shuffle-button:hover {
  opacity: 0.9;
  cursor: pointer;
}
